import React from 'react'
import burgerlogo from '../../assets/images/burger-logo.png'
import styles from './Logo.module.css'

const logo = (props) => {
    return (
        <div className={styles.Logo} style={{height:props.height}}>
           <img src={burgerlogo} alt="MyBurger" />    
        </div>
    )
}

export default logo
     